@import '../../var';

.About {
  > div {
    margin-top: $space;

    &:first-child {
      margin-top: 0;
    }
  }
}

.TwoColumnGrid {
  display: grid;
  grid-template-columns: repeat(2, calc((100% - #{$space}) / 2));
  grid-gap: $space;

  @include mobile {
    grid-template-columns: 100%;
  }
}

.TwoColumnFlex {
  @include flex(column, flex-start, flex-start);
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0;
  }

  > * {
    &:first-child {
      flex-shrink: 0;
      margin-right: 1rem;
    }
  }

  @include desktop {
    flex-direction: row;
  }

  :global(.RedBold) {
    margin-bottom: 0.2rem;

    & + * {
      padding-left: 1rem;

      @include desktop {
        padding-left: 0;
      }
    }
  }

  :global(.Bold) {
    margin-bottom: 0.2rem;
  }
}

.EducationExperience {
  > *:not(:last-child) {
    margin-bottom: $space;
  }

  .year ~ * {
    padding-left: $space;
  }

  .item {
    > *:not(:last-child) {
      margin-bottom: $space/4;
    }
  }

  .bullet {
    max-width: 800px;
  }

  a {
    color: inherit;
    &:hover {
      color: $color-accent;
    }
  }
}

.Skills {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1rem;

  @include mobile {
    grid-template-columns: auto;
  }

  :global(.Bold) {
    width: 100px;
  }
}

.Languages {
  :global(.RedBold) {
    display: inline-block;
    width: 56px;
    margin-right: 1rem;
  }
}
